.main {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  overflow: scroll;
  max-width: 1100px;
  /* background-image: linear-gradient(30deg, #211010, #5e2222); */
  /* background-color: #333; */
  scroll-snap-type: y mandatory;
}

.card {
  margin: 0.5rem;
  min-height: 400px;
  height: min;
  width: 300px;
  background-image: linear-gradient(30deg, #211010, #5e2222);
  box-shadow: 0px 3px 10px #fff1;
  border: 1px solid #666;
  border-radius: 0.5rem;
  overflow: hidden;
  cursor: pointer;
  scroll-snap-align: start;
}

.image {
  height: 200px;
  width: 100%;
  background-image: url('../../public/web.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  filter: grayscale(100%);
}
